.movies__input-container {
    width: 100%;
    padding: 70px 0 15px;
    border-bottom: 1px solid #E8E8E8;
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
}
.movies__search-icon  {
    background-image: url(../../images/searchIcon.svg);
    border: 1px solid #E8E8E8;
    border-radius: 48px;
    width: 34px;
    height: 34px;
    background-repeat: no-repeat;
    background-position: center;
}
.movies__search-input {
    border: none;
    text-decoration: none;
    font-weight: 500;
    font-size: 28px;
    line-height: 34px;
    color: black;
    text-align: center;
    outline: none;
    width: 600px;
}
.movies__search-input::-webkit-input-placeholder {
    color: #A0A0A0;
}
.movies__search-input::-moz-placeholder { 
    color: #A0A0A0; 
}
.movies__filters {
    display: flex;
    justify-content: center;
    gap: 15px;
}
.movies__search-input:focus::-webkit-input-placeholder { color:transparent; }
.movies__search-input:focus:-ms-input-placeholder { color:transparent; }
.movies__search-input:focus:-moz-placeholder { color:transparent; }

.movies__search-button {
    width: 60px;
    height: 34px;
    background: #3456F3;
    border-radius: 48px;
    border: none;
    font-weight: 500;
    font-size: 11px;
    line-height: 18px;
    text-align: center;
    color: #FFFFFF;
    transition: visibility 0s, opacity 0.5s;
    cursor: pointer;
}
.movies__search-button:hover {
    opacity: .8;
}
.movies__search-button_disabled {
    background: #F8F8F8;
}
.movies__search-button_disabled:hover {
    opacity: 1;
}
@media screen and (max-width: 980px) {
    .movies__input-container {
        padding: 86px 0 15px;
    }
    .movies__search-input {
        width: 440px;
    }

}
@media screen and (max-width: 750px) {
    .movies__input-container {
        padding: 98px 0 14px;
    }
    .movies__search-input {
        width: 440px;
    }
    .movies__search-icon  {
        display: none;
    }
    .movies__search-input {
        font-size: 18px;
        line-height: 22px;
        width: 220px;
        text-align: start;
    }
    .movies__input-container {
        padding: 92px 0 9px;
        margin-bottom: 43px;
    }
    .movies__filters {
        flex-direction: column;
        align-items: center;
    }

}